.header__inner{
  display: flex;
  position: relative;
  z-index: 10;
  flex-direction:row;
  justify-content: flex-start;
  align-items: center;
}

.menu__btn{
  cursor: pointer;
  transition: 0.2s;
}
.menu__burger{
  display: inline-block;
  height: 7px;
  width: 24px;
  position: relative;
  margin-right: 16px;
  &:after, &:before{
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-color: #fff;
    position: absolute;
  }
  &:after{
    left: 0;
    bottom: 0;
  }
  &:before{
    left: 0;
    top: 0;
  }
}
.menu__title{
  font-family: $font-light;
  font-size: 12px;
  text-transform: uppercase;
  color:white;
  display: inline-block;
}

.header__logo{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.lang{
  font-family: $font-light;
  font-size: 12px;
  text-transform: uppercase;
  color: white;
  margin-left: 91px;
}

.header__phone{  
  margin-left: auto;
}
.phone{
  display: block;
  line-height: 24px;
  height: 24px;
  font-family: $font-light;
  font-size: 12px;
  color: white;
  padding-right: 32px;
  background: right center no-repeat url('../images/whatsapp_icon.svg');
}
.header__request{
  margin-left: 88px;
}
.request{
  display: block;
  border: 1px solid white;
  font-family: $font-light;
  font-size: 12px;
  color: white;
  width: 136px;
  height: 32px;
  line-height: 32px;
  text-align: center;
}

.header__search{
  margin-left: 97px;
}
.search{
  padding-left: 22px;
  background: left center no-repeat url('../images/search_icon.svg');
}
.search__input{
  background-color:transparent;
  border: none;
  font-family: $font-light;
  font-size: 12px;
  color: white;
  &::placeholder{
    font-size: 12px;
    font-family: $font-light;
    text-transform: uppercase;
    opacity: 0.6;
    color: white;
  }
}