*, *:after, *:before{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  padding: 0;
  margin: 0;
  overflow-x: hidden;
}

h1, h4, h3{  
  font-family: 'Cera PRO Medium', sans-serif;
  letter-spacing: -0.02em;
}
h1{ 
  font-size: 88px;  
}
h4{
  font-size: 72px;
}
h3{
  font-size: 56px;
}

h2{
  font-family: 'Cera PRO Regular', sans-serif;
  font-weight: 500;
  font-size: 48px;
}
.h6_regular_middle{
  font-family: 'Cera PRO Regular', sans-serif;
  font-weight: 400;
  font-size: 24px;
  letter-spacing: -0.02em;
}
.h6_medium_middle{
  font-family: 'Cera PRO Medium', sans-serif;
  font-weight: 500;
  font-size: 24px;
  letter-spacing: -0.02em;
}
.h6_regular_small{
  font-family: 'Cera PRO Regular', sans-serif;
  font-weight: 400;
  font-size: 12px;
  letter-spacing: -0.02em;
}
.h5_light{
  font-family: 'Cera PRO Light', sans-serif;
  font-weight: 300;
  font-size: 12px;
  letter-spacing: -0.02em;
}
p{
  font-family: 'Cera PRO Light', sans-serif;
  font-weight: 300;
  font-size: 15px;
  letter-spacing: -0.02em;
}
.description{
  font-family: 'Cera PRO Light', sans-serif;
  font-size: 12px;
  line-height: 160%;
  letter-spacing: -0.02em;
}
a{
  text-decoration: none;
}
