.card{
  //max-width: 50%;
}
.card__desc{
  text-transform: uppercase;
  font-family: $font-light;
  font-weight: 300;
  @include lineAndSpacing();
  font-size: 12px;
  margin-bottom: 24px;
}
.card__title{
  font-family: $font-regular;
  font-size: 48px;
  @include lineAndSpacing(110%);
  margin-bottom: 32px;
}
.card__text{
  font-family: $font-light;
  font-size: 14px;
  max-width: 416px;
  @include lineAndSpacing(160%);
}
.card__link{
  text-transform: uppercase;
  font-family: $font-regular;
  font-size: 12px;
  margin-top: 48px;
  display: block;
  
  @include lineAndSpacing();
  span{
    margin-right: 16px;
  }
}
.card__link_dark{
  color: #1B1B1B;
}
.card__link_white{
  color: white;
}
.card_dark{
  color: #1B1B1B;
}
.card_white{
  color: white;
}
