.wrapper{
   width: calc(1312 / 1440 * 100%);
   margin: 0 auto;
}
.container{
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
}
.container_space_btw{
  justify-content: space-between;
}
.container_align_center{
  align-items: center;
}

.container__col_5{
  width: calc(100% / 24 * 5);
}
.container__col_4{
  width: calc(100% / 24 * 4);
}

.menu{
  list-style-type: none;
}
.menu__item{
  margin-bottom: 16px;
}
.menu__link{
  color: white;
  font-family: $font-light;
  font-size: 14px;
  @include lineAndSpacing(160%);
}
.menu__link_medium{
  font-family: $font-medium;
}

//-------------------Footer---------------

.footer{
  background-color: #333333;
  padding: 64px 0 0;
}
.footer__link_description{
  opacity: 0.6;
  color: white;
}
.footer__privacy{
  margin-top: 64px;
  padding-bottom: 19px;  
  border-bottom: 1px solid rgba(255,255,255,0.2);
}

.footer__privacy_text_right{
  text-align: right;
}

.footer__socials{
  padding: 16px 0;
}

.logo_tftl{
  display: block;
  margin-left: auto;
  align-self: flex-end;
}
