.section_bg_dark{  
  background-color: #333333;
}
.section__card{
  max-width: 636px;
  margin-left: auto;
}

//  Section main

.section_main{
  width: 100vw;
  height: 100vh;
  position: relative;
  padding: 32px 0 40px;
}
.swiper-container_main{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: url('../../assets/images/cursor.svg');
}
.swiper-wrapper_main{
  width: 100%;
  height: 100%;
  position: relative;
}
.swiper-slide{
  background-position: center 20%;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  &__inner{
    background-image:linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
    background-position: center;
    position: relative;
    width: 100%;
    height: 100%;
  }
  &__image{
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      left: 0;
      top: 50%;
      transform: translateY(-20%);
      display: block;
      z-index: -1;
    }
    &:after{
      content: '';
      position: absolute;
      display: block;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-image:linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
    }
  }
  &__title{
    font-family: $font-medium;
    font-size: 72px;
    font-weight: 500;
    @include lineAndSpacing(100%);
    color: white;
    display: block;
    position: absolute;
    left: 4.72%;
    top: 28.63%;
    max-width: 932px;
  }
  &__info{
    position: absolute;
    top: 56.58vh;    
    left: 41.81%;
    color: white;
    display: flex;
    flex-direction: row;
  }
  &__index{
    font-family: $font-light;
    font-size: 12px;
    min-width: 46px;
    margin-right: 32px;
  }
  &__text{
    font-family: $font-light;
    font-size: 14px;
    @include lineAndSpacing(160%);
    max-width: 248px;
  }
}
.wrapper_section_main{
  height: 100%;
  position: relative;
  pointer-events: none;
}
.socials_section_main{
  margin-top: auto;
  position: absolute;
  bottom: 32px;
  left: 0;
}
//  Section about

.section_about{
  padding: 156px 0;
}

//  Section services

.services__container{
  width: 100%;  
  display: flex;
  justify-content: space-between;
  margin-bottom: -130px;
  position: relative;
  z-index: 1;
}
.service{
  width: calc(416 / 1312 * 100%);
  position: relative;
  overflow: hidden;
  cursor: pointer;
  &:after{
    content: '';
    display: block;
    width: 100%;
    padding-top: 133.4%;
  }
  &__inner{
    position: absolute;
    top:0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 40px;
    color: white;
    display: flex;
    flex-direction: column;
  }
  &__image{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    &:after{
      content: '';
      display:block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
    }
    img{
      display: block;
      width: 100%;
      object-fit: cover;
    }
  }
  &__desc{
    font-family: $font-light;
    @include lineAndSpacing(160%);
    opacity: 0;    
    transition: 0.2s;
  }
  &__index{
    font-family: $font-light;
    font-size: 12px;
    text-transform: uppercase;
    margin-top: auto;
    padding-top: 32px;
    border-top: 1px solid rgba(255,255,255, 0);
    transition: 0.2s;
  }
  &__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 16px;
  }
  &__title{
    font-family: $font-regular;
    font-size: 24px;
    @include lineAndSpacing(120%);
  }
  &__arrow{
    width: 66px;
    height: 11px;
    background-position: right center;
    background-repeat: no-repeat;
    background-image: url('../images/arrow_right_white.svg');    
    transition: 0.2s;
    opacity: 0;
  }
  &:hover{
    .service{
      &__desc{
        opacity: 0.6;
      }
      &__index{
        border-top: 1px solid rgba(255,255,255,.2);
      }
      &__arrow{
        opacity: 1;
      }
    }
  }
}
//  Section comunity

.section_comunity{
  padding-bottom: 72px;
  padding-top: 156px;
}
.section__slider_comunity{
  margin-top: 104px;
}

//  Section technologies
.section_tech{  
  padding-top: 286px;
}
.section__techs{
  margin-top: 104px;
  position: relative;
}

.techs__list{
  position: relative;
  z-index: 2;
}
.tech__item{
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  color: white;
  padding: 40px 0;
  border-top: 1px solid rgba(255,255,255,.2);
  transition: 0.2s;
  cursor: pointer;
  &:last-child{
    border-bottom: 1px solid rgba(255,255,255,.2);
    
  }
  &:hover{
    // .tech__desc{
    //   opacity: 1;
    // }
    // .tech__title{
    //   opacity: 1;
    // }
    // .tech__index{
    //   opacity: 1;
    // }
    *{
      opacity:1;
    }
  }
}
.tech__index{
  font-family: $font-light;
  font-size: 12px;
  align-self: flex-start;
  text-transform: uppercase;
  opacity: 0.6;
  transition: 0.2s;
}
.tech__title{
  font-family: $font-medium;
  font-size: 56px;
  margin-left: 32px;
  opacity: 0.6;
  transition: 0.2s;
}
.tech__desc{
  font-family: $font-light;
  font-size: 12px;
  opacity: 0.6;
  max-width: 304px;
  margin-left: auto;
  @include lineAndSpacing(160%);
  opacity: 0;
  transition: 0.2s;
}

.tech__images{
  position: absolute;
  width: 350px;
  height: 254px;
  transform: translateY(-50%) rotate(6deg);
  overflow: hidden;
  top: 50%;
  left: 43.125%;
  z-index: 1;
}
.tech__image{
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
}


// Section map
.section_map{
  padding: 156px 0;
  position: relative;
  width: 100%;
  height: 1168px;
}
.map{
  position: absolute;
  width: 100%;
  z-index:-1;
}
.map__image{
  width: 100%;
  display: block;
  object-fit: cover;
}
